import React, { Fragment } from 'react';
import { Link as GatsbyLink } from 'gatsby';
import { Box, Divider, styled, Typography, useTheme } from '@mui/material';
import { usePageRoute } from 'hooks/usePageRoute';

interface Props {
  title: string;
  date: string;
  categories: readonly (string | null)[];
  viewCount?: number;
}
const PostHeader = ({ title, date, categories, viewCount }: Props) => {
  const { spacing, palette, typography } = useTheme();

  const { getCategoryPath } = usePageRoute();

  return (
    <Box component="header" sx={{ marginBottom: spacing(5) }}>
      <Box sx={{ color: palette.grey['500'] }}>
        <Box component="time" sx={{ fontSize: typography.body2.fontSize, marginRight: spacing(1) }}>
          {date}
        </Box>
        <span>
          {categories.map((category, i) =>
            category == null ? null : (
              <Fragment key={i}>
                <Link to={getCategoryPath(category)}>{category.toUpperCase()}</Link>
                {i !== categories.length - 1 && ' / '}
              </Fragment>
            )
          )}
        </span>
      </Box>
      <Typography
        component="h1"
        fontFamily={`"Nanum Myeongjo", serif`}
        fontSize={typography.h5.fontSize}
        fontWeight={typography.fontWeightBold}
        sx={{ margin: `0 0 ${spacing(2)}` }}
      >
        {title}
      </Typography>
      {viewCount && (
        <Typography variant="caption" component="p" sx={{ margin: `0 0 ${spacing(1)}` }}>
          <strong>{viewCount.toLocaleString('ko-KR')}</strong>명의 사람들이 읽어봤어요.
        </Typography>
      )}
      <Divider />
    </Box>
  );
};

const Link = styled(GatsbyLink)(({ theme: { typography, palette } }) => ({
  color: 'inherit',
  textDecoration: 'none',
  fontSize: typography.caption.fontSize,
  '&:hover': {
    color: palette.primary.main,
  },
}));

export default PostHeader;
